header {
  margin-bottom: var(--spacing-lg);
}

header h1,
header h2 {
  margin: 0;
  font-size: 1.8rem;
}

header h2 {
  font-weight: var(--font-weight-light);
}

h3 {
  font-size: 1.5rem;
}

p {
  line-height: 1.5rem;
}

/* icons */
i {
  margin-right: var(--spacing-sm);

  &.right {
    margin-right: 0;
    margin-left: var(--spacing-sm);
  }
}

i.large {
  font-size: 2rem;
  margin-right: var(--spacing);
}

ul.styled-ul {
  li {
    list-style: none;
    position: relative;
    padding-left: var(--spacing-md);
    margin-bottom: var(--spacing);
    margin-left: var(--spacing);

    &:before {
      position: absolute;
      top: 0.4rem;
      left: 0;
      content: " ";
      height: 0.6rem;
      width: 0.6rem;
      background-color: var(--color-blue-light);
    }
  }
}
